@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
:root {
  --primary-color: #00acdc;
  --black-color: #000;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  position: relative;
}

.nav__item:first-of-type {
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  color: var(--black-color);
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover, .is-active {
  color: var(--primary-color);
}

.is-active:after {
  content: "";
  background-color: var(--primary-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

